#TimeBox {
    position: relative;
    padding: 0;
}
.ruler {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.ruler div {
    height: 50px;
    border-left: 2px solid black;
    padding: 5px;
    width: 100%;
}
#TimeBox .TimeBox-el {
    position: relative;
    width: -webkit-calc(100vw - 35px);
    width: -moz-calc(100vw - 35px);
    width: -o-calc(100vw - 35px);
    width: calc(100vw - 35px);
    height: 0;
}
#TimeBox .x {
    position: absolute;
    width: 100%;
    left: 0px;
    transition: transform 8s, opacity ease 8s;
    transform: translate3d(100%, 10px, 0px);
    opacity: 0.06;
}
#TimeBox .x.opacity{
    visibility: hidden;
    opacity:1;
}

#TimeBox .x div:first-child {
    visibility: visible;
    width: 40px;
}
.del{
    text-align: center;
    padding: 8px;
    position: absolute;
    left: 47%;
    right: 47%;
}
.droptarget {
    width: 100vw;
}
.TimeBox-el .ddcontainer {
    width: 100%;
}
@media  only screen and (max-width: 600px) {
    .ruler div {
        word-wrap: anywhere;
        white-space: nowrap;
        /*text-overflow: ellipsis;*/
        overflow: hidden;
        border-left: 2px solid black;
        border-bottom: 2px solid black;
        padding: 2px;
    }
}